* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  margin-left: 15px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
li {
  list-style: none;
  padding: 0;
}
.country-list {
  /* display: flex; */
  /* margin: 0; */
  padding: 0;
  /* margin: 0px;  */
}
.image-wrap {
  display: flex;
  align-items: center;
}
img {
  margin-right: 10px;
}
